body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  margin: 12px
}

.App-link {
  color: #61dafb;
}

input[type='file'] {
  cursor: pointer;
  touch-action: manipulation;
  margin: 0;
  padding: 0;
  overflow: visible;
}

.upload-button {
  width: 200px;
  margin: 0 auto;
  padding: 8px;
  text-align: center;
  vertical-align: middle;
}

.upload-button button{
  width: 200px;
  height: 60px;
  padding: 8px;
  text-align: center;
  vertical-align: middle;
  background: none;
}

.file-list {
  height: 100%;
  width: 100%;
  border: 1px solid #666666;
  margin-top: 24px;
}

.file-list .file {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  width: 100%;
  border-bottom: 1px dashed #666666
}

.file-list .file .layout {
  width: 50%;
}

.file-list .file p {
  padding-left: 24px;
}

.file-list .file img {
  width: 100%;
}

.file-list .file .copy, .file-list .file .delete {
  display: block;
  float: left;
  margin: 15px 15%;
}

.file-list .file .qrcode {
  display: block;
  margin: 0 auto;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

